<template>
  <div>
    <form-wizard
      ref="checkinFormWizard"
      color="#0E2551"
      :title="null"
      :subtitle="null"
      finish-button-text="Salva"
      back-button-text="Indietro"
      next-button-text="Avanti"
      class="steps-transparent mb-3"
      @on-complete="formSubmitted"
      @on-change="wizardOnChangeTab"
    >
      <tab-content
        title="Cassetta sorgente"
        icon="feather icon-inbox"
        :before-change="validateSourceBox"
      >
        <validation-observer ref="sourceBoxRules" tag="form">
          <b-row>
            <b-col cols="12">
              <b-form-group
                label="Cassetta sorgente"
                label-for="source_box"
                label-cols-md="4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="form.source_box"
                  rules="required"
                >
                  <b-input-group>
                    <b-input-group-prepend is-text>
                      <feather-icon icon="GridIcon" />
                    </b-input-group-prepend>
                    <v-select
                      id="source_box"
                      v-model="form.source_box"
                      label="code"
                      placeholder="Seleziona l'identificativo dalla lista o passa la cassetta sulla bilancia"
                      :options="possibleSourceBoxes"
                      class="singleFull"
                      @input="onSourceBoxChange([$event])"
                    >
                      <template #option="{ code, good_id }">
                        <span>{{ code }}</span>
                        <span v-if="good_id !== null" class="text-nowrap">
                          ({{ elementAt("goods", good_id, "plural") }})</span
                        >
                      </template>
                    </v-select>
                  </b-input-group>
                  <small v-if="errors[0]" class="text-danger"
                    >É necessario scegliere una cassetta sorgente</small
                  >
                </validation-provider>
              </b-form-group>
            </b-col>
          </b-row>
        </validation-observer>
      </tab-content>

      <tab-content
        title="Cassetta di destinazione"
        icon="feather icon-inbox"
        :before-change="validateDestinationBox"
      >
        <validation-observer ref="destinationBoxRules" tag="form">
          <b-row>
            <b-col cols="12">
              <b-form-group
                label="Cassetta di destinazione"
                label-for="destination_box"
                label-cols-md="4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="form.destination_box"
                  rules="required"
                >
                  <b-input-group>
                    <b-input-group-prepend is-text>
                      <feather-icon icon="GridIcon" />
                    </b-input-group-prepend>
                    <v-select
                      id="destination_box"
                      v-model="form.destination_box"
                      label="code"
                      placeholder="Seleziona l'identificativo dalla lista o passa la cassetta sulla bilancia"
                      :options="possibleDestinationBoxes"
                      class="singleFull"
                      @input="onDestinationBoxChange([$event])"
                    >
                      <template #option="{ code, good_id }">
                        <span> {{ code }}</span>
                        <span v-if="good_id == null"> (vuota)</span>
                        <span v-if="good_id !== null"> (stesso contenuto)</span>
                      </template>
                    </v-select>
                  </b-input-group>
                  <small v-if="errors[0]" class="text-danger"
                    >É necessario scegliere una cassetta di destinazione</small
                  >
                </validation-provider>
              </b-form-group>
            </b-col>
          </b-row>
        </validation-observer>
      </tab-content>

      <tab-content
        title="Inserimento peso"
        icon="feather icon-box"
        :before-change="validateGoodWeight"
      >
        <validation-observer ref="goodWeightRules" tag="form">
          <b-col cols="12">
            <small
              >Hai scelto la cassetta {{ form.source_box.code }} che contiene
              {{ form.source_box.good_weight }} Kg. di
              {{ elementAt("goods", form.source_box.good_id, "plural") }}.</small
            >
            <small class="text-danger">
              Non potrai quindi spostare più di
              {{ form.source_box.good_weight }} Kg.</small
            ><br /><br />

            <b-form-group
              :label="
                'Peso merce da spostare (max: ' + form.source_box.good_weight + ' Kg.)'
              "
              label-for="good_weight"
              label-cols-md="4"
            >
              <validation-provider
                #default="{ errors }"
                name="form.good_weight"
                rules="required"
              >
                <b-input-group>
                  <b-input-group-prepend is-text>
                    <feather-icon icon="GridIcon" />
                  </b-input-group-prepend>
                  <cleave
                    id="good_weight"
                    v-model="form.good_weight"
                    placeholder="Inserisci il peso della merce o pesala sulla bilancia"
                    :options="number"
                    value=""
                    class="doubleFull cleave"
                    numeric-only
                  />
                  <b-input-group-append is-text> Kg. </b-input-group-append>
                  <b-button id="buttonMoveAll" variant="info" @click="moveAll">
                    Travasa tutto
                  </b-button>
                </b-input-group>
                <small v-if="errors[0]" class="text-danger"
                  >É necessario inserire un peso inferiore a
                  {{ form.source_box.good_weight }} Kg.</small
                >
              </validation-provider>
            </b-form-group>
          </b-col>
        </validation-observer>
      </tab-content>

      <tab-content title="Conferma" icon="feather icon-check">
        <b-card no-body>
          <b-card-header>
            <b-card-title>
              <h2>Verifica valori impostati</h2>
              <br />
            </b-card-title>
          </b-card-header>
          <b-card-body>
            <dl class="row">
              <dt class="col-sm-3">Cassetta sorgente:</dt>
              <dd class="col-sm-9">
                {{ form.source_box.code }}

                <b-badge
                  v-if="form.source_box.color !== undefined"
                  class="infobadge"
                  :title="form.source_box.color"
                  variant="light-secondary"
                >
                  <span
                    ><small>{{ form.source_box.color }}</small></span
                  >
                </b-badge>

                <b-badge
                  v-if="form.source_box.tare !== undefined"
                  class="infobadge"
                  :title="form.source_box.tare"
                  variant="light-secondary"
                >
                  <span
                    ><small>Tara: {{ form.source_box.tare }} Kg.</small></span
                  >
                </b-badge>

                <b-badge
                  v-if="form.good_weight !== undefined"
                  class="infobadge"
                  :title="form.good_weight"
                  variant="light-danger"
                >
                  <span
                    >- {{ form.good_weight }} Kg. =
                    {{ view_source_good_weight }} Kg.</span
                  >
                </b-badge>
              </dd>
            </dl>
            <dl class="row">
              <dt class="col-sm-3">Cassetta destinazione:</dt>
              <dd class="col-sm-9">
                {{ form.destination_box.code }}

                <b-badge
                  v-if="form.destination_box.color !== undefined"
                  class="infobadge"
                  :title="form.destination_box.color"
                  variant="light-secondary"
                >
                  <span
                    ><small>{{ form.destination_box.color }}</small></span
                  >
                </b-badge>

                <b-badge
                  v-if="form.destination_box.tare !== undefined"
                  class="infobadge"
                  :title="form.destination_box.tare"
                  variant="light-secondary"
                >
                  <span
                    ><small>Tara: {{ form.destination_box.tare }} Kg.</small></span
                  >
                </b-badge>

                <b-badge
                  v-if="form.good_weight !== undefined"
                  class="infobadge"
                  :title="form.good_weight"
                  variant="light-success"
                >
                  <span
                    >+ {{ form.good_weight }} Kg. =
                    {{ view_destination_good_weight }} Kg.</span
                  >
                </b-badge>
              </dd>
            </dl>
            <dl class="row">
              <dt class="col-sm-3">Merce spostata:</dt>
              <dd class="col-sm-9">
                {{ view_good }}

                <b-badge
                  v-if="
                    elementAt('countries', form.source_box.good_origin, 'name') !== ''
                  "
                  v-b-tooltip
                  class="infobadge infoflag"
                  :title="elementAt('countries', form.source_box.good_origin, 'name')"
                  variant="light-secondary"
                >
                  <!--<feather-icon icon="GlobeIcon" />-->
                  <country-flag
                    :country="elementAt('countries', form.source_box.good_origin, 'code')"
                    size="small"
                    rounded
                  />
                  <!--<small>{{elementAt("countries", props.row.good_origin, "code")}}</small>-->
                </b-badge>

                <b-badge
                  v-if="
                    elementAt(
                      'goodcategories',
                      form.source_box.good_category,
                      'description'
                    ) !== ''
                  "
                  v-b-tooltip
                  class="infobadge"
                  :title="
                    elementAt(
                      'goodcategories',
                      form.source_box.good_category,
                      'description'
                    )
                  "
                  variant="light-secondary"
                  ><feather-icon icon="AwardIcon" />
                  <small>{{ form.source_box.good_category, }}</small>
                </b-badge>

                <b-badge
                  v-if="form.source_box.good_price !== undefined"
                  v-b-tooltip
                  class="infobadge"
                  :title="form.source_box.good_price + ' €'"
                  variant="light-secondary"
                  ><!--<feather-icon icon="DollarSignIcon" />-->
                  <span>{{ form.source_box.good_price }}<small> €</small></span>
                </b-badge>
              </dd>
            </dl>
            <dl class="row">
              <dt class="col-sm-3">Peso merce spostata:</dt>
              <dd class="col-sm-9">{{ form.good_weight }} Kg.</dd>
            </dl>
          </b-card-body>
        </b-card>
      </tab-content>
    </form-wizard>
  </div>
</template>

<script>
import { elementAt, boxLocationToReadable } from "../utils";

import { FormWizard, TabContent } from "vue-form-wizard";
import vSelect from "vue-select";

import { ValidationProvider, ValidationObserver } from "vee-validate";
import Cleave from "vue-cleave-component";
import Ripple from "vue-ripple-directive";

import "vue-form-wizard/dist/vue-form-wizard.min.css";

import {
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BInputGroup,
  BInputGroupPrepend,
  BInputGroupAppend,
  BFormCheckbox,
  BForm,
  BCard,
  BCardTitle,
  BCardText,
  BCardHeader,
  BCardBody,
  BButton,
  BBadge,
  VBTooltip,
  BFormInvalidFeedback,
} from "bootstrap-vue";
import { required, email } from "@validations";

export default {
  components: {
    FormWizard,
    TabContent,
    BRow,
    BCol,
    BInputGroup,
    BFormGroup,
    BFormInput,
    BInputGroupPrepend,
    BInputGroupAppend,
    BFormCheckbox,
    BForm,
    BButton,
    BCard,
    BCardTitle,
    BCardText,
    BCardHeader,
    BCardBody,
    BBadge,
    vSelect,
    Cleave,

    ValidationProvider,
    ValidationObserver,
    BFormInvalidFeedback,
  },
  directives: {
    Ripple,
    "b-tooltip": VBTooltip,
  },
  data() {
    return {
      number: {
        numeral: true,
        numeralThousandsGroupStyle: "thousand",
        autocomplete: "off",
      },
      form: {
        source_box: "",
        destination_box: "",
        good_weight: "",
      },
      view_good: null,
      view_source_good_weight: 0,
      view_destination_good_weight: 0,
      countries: [],
      boxes: [],
      possibleDestinationBoxes: [],
      possibleSourceBoxes: [],
      goods: [],
      goodCategories: [],
      required,
      pars: [],
      step: -1,
    };
  },
  watch: {
    possibleDestinationBoxes(val) {
      const self = this;
      console.log("possibleDestinationBoxes changed:", self.possibleDestinationBoxes);
    },
    possibleSourceBoxes(val) {
      const self = this;
      console.log("possibleSourceBoxes changed:", self.possibleDestinationBoxes);
    },
  },
  beforeCreate() {
    const self = this;

    this.$store.dispatch("boxes/get").then((result) => {
      console.log(result);
      self.boxes = result.data.data;
      self.processPossibleSourceBoxes();
    });

    this.$store.dispatch("goods/get").then((result) => {
      console.log(result);
      self.goods = result.data.data;
    });

    this.$store.dispatch("countries/get").then((result) => {
      console.log(result);
      self.countries = result.data.data;
    });

    this.$store.dispatch("goodcategories/get").then((result) => {
      console.log(result);
      self.goodCategories = result.data.data;
    });
  },
  setup() {
    const self = this;
    // console.log(this.$store);
  },
  created() {
    var self = this;
    window.$eventBus.$on("box", (data) => {
      console.log("👂 received box", data);
      if (self.step == -1) {
        self.form.source_box = data;
        self.sourceBoxChanged();
      } else if (self.step == 0) {
        self.form.destination_box = data;
        self.destinationBoxChanged();
      }
    });

    window.$eventBus.$on("weight", (data) => {
      console.log("👂 received weight", data);
      self.form.good_weight = data;
    });
  },  
  destroyed() {
    console.log("⏩ destroyed");
    window.$eventBus.$off("weight");
    window.$eventBus.$off("box");
  },
  methods: {
    validateSourceBox() {
      return new Promise((resolve, reject) => {
        this.$refs.sourceBoxRules.validate().then((success) => {
          if (success) {
            resolve(true);
          } else {
            reject();
          }
        });
      });
    },
    validateDestinationBox() {
      return new Promise((resolve, reject) => {
        this.$refs.destinationBoxRules.validate().then((success) => {
          if (success) {
            resolve(true);
          } else {
            reject();
          }
        });
      });
    },
    validateGoodWeight() {
      const self = this;
      console.log(self.form.good_weight);
      return new Promise((resolve, reject) => {
        console.log("evaluating");
        if (
          self.form.good_weight == "" ||
          self.form.good_weight == 0 ||
          self.form.good_weight > self.form.source_box.good_weight
        ) {
          console.log("rejected");
          reject();
        } else {
          console.log("approved");
          resolve(true);
        }
      });
    },
    processPossibleSourceBoxes() {
      const self = this;
      self.possibleSourceBoxes = [];
      for (const box of self.boxes) {
        if ("good_id" in box && box.good_id != null) {
          //box is not empty
          self.possibleSourceBoxes.push(box);
        }
      }
    },
    elementAt,
    boxLocationToReadable,
    onSourceBoxChange(event) {
      console.log("source box change", event);
      this.sourceBoxChanged();
    },
    sourceBoxChanged() {
      const self = this;
      self.possibleDestinationBoxes = [];

      if (self.form.source_box != null) {
        console.log(self.boxes);

        for (const box of self.boxes) {
          if ("good_id" in box && box.good_id != null) {
            if (
              box.good_id == self.form.source_box.good_id &&
              box.code != self.form.source_box.code
            ) {
              self.possibleDestinationBoxes.push(box);
            }
          } else {
            self.possibleDestinationBoxes.push(box);
          }
        }

        console.log(self.possibleDestinationBoxes);
      }
    },
    onDestinationBoxChange(event) {
      console.log("destination box change", event);
      this.destinationBoxChanged();
    },
    destinationBoxChanged() {
      const self = this;

      self.pars = {};
      self.pars.source = self.form.source_box;
      self.pars.destination = self.form.destination_box;

      if (self.pars.destination.good_weight == null) {
        self.pars.destination.good_weight = 0;
      }

      console.log("pars", self.pars);

      self.view_good = elementAt("goods", self.pars.source.good_id, "plural");
    },
    wizardOnChangeTab(step) {
      const self = this;
      self.step = step;

      console.log("step:", step);

      if (step == 2) {
        console.log(
          self.pars.source.good_weight,
          self.form.good_weight,
          self.form.good_weight * 1 + 0,
          self.pars.source.good_weight + self.form.good_weight * 1 + 0
        );
        self.view_source_good_weight =
          self.pars.source.good_weight - self.form.good_weight * 1 + 0;
        self.view_destination_good_weight =
          self.pars.destination.good_weight + self.form.good_weight * 1 + 0;
      }
    },
    moveAll() {
      const self = this;
      self.form.good_weight = self.pars.source.good_weight;
      console.log(self.form.good_weight);
    },
    formSubmitted() {
      const self = this;

      const sourceToSend = JSON.parse(JSON.stringify(self.pars.source));
      sourceToSend.box_code = sourceToSend.code;
      delete sourceToSend.code;
      delete sourceToSend._id;
      delete sourceToSend.__v;
      sourceToSend.good_weight = self.view_source_good_weight;

      const destinationToSend = JSON.parse(JSON.stringify(self.pars.destination));
      destinationToSend.box_code = destinationToSend.code;
      delete destinationToSend.code;
      delete destinationToSend._id;
      delete destinationToSend.__v;
      destinationToSend.good_weight = self.view_destination_good_weight;

      // overwrite all the infomation I have from the souce box. In the future this might be asked.
      destinationToSend.good_id = sourceToSend.good_id;
      destinationToSend.good_category = sourceToSend.good_category;
      destinationToSend.good_origin = sourceToSend.good_origin;
      destinationToSend.good_price = sourceToSend.good_price;

      // for now location is not changed. In case a flow should be added.

      // sourceToSend is edited again after having copied the info to destinationToSend
      if (sourceToSend.good_weight <= 0) {
        sourceToSend.good_id = null;
        sourceToSend.good_category = null;
        sourceToSend.good_origin = null;
        sourceToSend.good_price = null;
        sourceToSend.good_weight = null;
      }

      console.log("- source parameters", sourceToSend);
      console.log("- destination parameters", destinationToSend);

      self.$store.dispatch("transactions/add", sourceToSend).then((result1) => {
        console.log("- source transaction added", result1);
        self.$store.dispatch("transactions/add", destinationToSend).then((result2) => {
          console.log("- destination transaction added", result2);
          self.dataSaved();
        });
      });
    },
    dataSaved() {
      const self = this;
      this.$swal({
        title: "Refill completato",
        text: "Vuoi eseguire un altro refill?",
        icon: "success",
        showCancelButton: true,
        confirmButtonText: "Si",
        cancelButtonText: "No, torna alla home",
        customClass: {
          confirmButton: "btn btn-primary",
          cancelButton: "btn btn-outline-danger ml-1",
        },
        buttonsStyling: false,
      }).then((result) => {
        if (result.value) {
          this.$router.go(); // refresh route instead of this.$router.push({ name: "refill"}).catch(()=>{}); //catch to avoid redundant navigation
        } else {
          this.$router.push({ name: "home" });
        }
      });
    },
    handleValidation(isValid, tabIndex) {
      console.log(`Tab: ${tabIndex} valid: ${isValid}`);
    },
    handleErrorMessage(errorMsg) {
      console.log(`Error: ${errorMsg}`);
      this.errorMsg = errorMsg;
    },
  },
};
</script>

<style lang="scss">
@import "@core/scss/vue/libs/vue-wizard.scss";
@import "@core/scss/vue/libs/vue-select.scss";
@import "@core/scss/vue/libs/vue-sweetalert.scss";

.singleFull {
  background-color: white;
  width: calc(100% - 43px);
}
.doubleFull {
  background-color: white;
  width: calc(100% - 96px);
}
.doubleFull.smaller {
  background-color: white;
  width: calc(100% - 110px);
}
.cleave {
  padding: 0 14px;
  outline: none;
  border: 1px solid #d8d6de;
}

#good_weight {
  width: 58%;
}

#buttonMoveAll {
  display: inline-block;
  margin-left: 10px;
}

#good_id {
  text-transform: capitalize;
}

.infobadge {
  margin-left: 3px;
}

.infobadge.infoflag .small-flag {
  margin: -1.2em -1.5em -1.2em;
}
</style>
